  export default {
    data() {
      return {
        // 
        swiperOption: {
          pagination: '.swiper-pagination',
          paginationClickable: true,
          autoplay: 4000,
          autoplayDisableOnInteraction: false,
          loop: true
        },
        msgs: {},
        hotmsgs: {},
        dialog: false
      }
    },
    created() {
      this.getBanner();
//      this.getHotProject()
    },
    methods: {
      // 
      getBanner() {
        this.$http.get(getBanner()).then(res => {
          this.msgs = res.data
        })
      },
      // 
//      getHotProject() {
//        this.$http.get(getHotProject()).then(res => {
//          this.hotmsgs = res.data
//        })
//      },
      // 
      open() {
        this.dialog = true
      },
      close() {
        this.dialog = false
      }
    },
    components: {
      swiper,
      swiperSlide,
//      hotProject,
      iTab,
      iLabel,
      iAll,
      Scroller,
      iInfor,
      iStart,
      projectItem
    }

  }



